*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

html {
  font-size: 16px;
  line-height: 1.2;
  font-weight: 400;
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -o-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  font-family: "Roboto", sans-serif;
  color: #000000;
  /* iOS fix */
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  -webkit-locale: auto;
  white-space: normal;
}

main {
  overflow: hidden;
  position: relative;
  z-index: 1;
}

a {
  color: #000000;
  text-decoration: none;
}

ul,
ol,
nav {
  list-style: none;
  padding: 0;
}

svg {
  width: auto;
  height: auto;
}

img {
  display: block;
  max-width: 100%;
}

input,
button {
  -webkit-appearance: none;
}

input,
button,
select,
textarea {
  font: inherit;
  background-color: transparent;
  border: 0;
  border-radius: 0;
  outline: 0;
}

input,
textarea {
  padding: 0;
  margin: 0;
}
input:focus::placeholder,
textarea:focus::placeholder {
  opacity: 0;
}

.container {
  max-width: 1190px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
  box-sizing: border-box;
  position: relative;
}

.body-scroll-lock {
  overflow: hidden;
}

/* Partials */
.header {
  width: 100%;
  z-index: 100;
  background-color: #ffffff;
}
.header__top {
  border-bottom: 1px solid #e9e9e9;
  padding: 1.75rem 0;
}
@media only screen and (max-width: 1079px) {
  .header__top {
    padding: 1.75rem 0.9375rem;
  }
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__burger {
  display: flex;
  align-items: center;
  width: 1.5625rem;
  height: 1rem;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
@media only screen and (max-width: 1079px) {
  .header__burger {
    order: 1;
  }
}
@media (min-width: 1080px) {
  .header__burger:hover, .header__burger:focus {
    opacity: 0.8;
  }
}
.header__burger.active span {
  background-color: transparent;
}
.header__burger.active span::before {
  transform: translateY(6px) rotate(45deg);
}
.header__burger.active span::after {
  transform: translateY(-8px) rotate(-45deg);
}
.header__burger span {
  display: block;
  width: 100%;
  height: 0.25rem;
  border-radius: 1.25rem;
  background-color: #000000;
  position: relative;
  transition: background-color 0.3s ease;
}
.header__burger span::before, .header__burger span::after {
  content: "";
  display: block;
  width: 100%;
  height: 0.25rem;
  border-radius: 1.25rem;
  background-color: #000000;
  position: absolute;
  transition: transform 0.3s ease;
}
.header__burger span::before {
  top: -0.4375rem;
}
.header__burger span::after {
  bottom: -0.4375rem;
}
.header__logo {
  height: 1.6875rem;
}
@media only screen and (max-width: 1079px) {
  .header__logo {
    width: 10.5rem;
  }
}
.header__logo svg {
  width: 100%;
}
.header__search {
  width: 1.1875rem;
  height: 1.1875rem;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
@media only screen and (max-width: 1079px) {
  .header__search {
    display: none;
  }
}
@media (min-width: 1080px) {
  .header__search:hover, .header__search:focus {
    opacity: 0.8;
  }
}

.menu {
  border-bottom: 1px solid #e9e9e9;
  background-color: #ffffff;
  position: sticky;
  top: 0;
  left: 0;
  z-index: 100;
  transition: transform 0.3s ease;
}
@media only screen and (max-width: 1079px) {
  .menu {
    position: absolute;
    top: 5.25rem;
    left: 0;
    width: 100%;
    height: calc(100vh - 84px);
    border-bottom: 0;
    transform: translateX(-100%);
    overflow: scroll;
  }
}
@media only screen and (max-width: 1079px) {
  .menu.open {
    transform: translateX(0);
  }
}
.menu__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media only screen and (max-width: 1079px) {
  .menu__container {
    padding: 0.875rem 1.8125rem;
  }
}
.menu__item {
  display: flex;
  margin: 0 auto;
}
@media only screen and (max-width: 1079px) {
  .menu__item {
    width: 100%;
    flex-direction: column;
    margin: 0;
  }
}
.menu__item > li {
  padding: 1.125rem 0;
  cursor: pointer;
}
@media only screen and (max-width: 1079px) {
  .menu__item > li {
    padding: 1.0625rem 0;
  }
}
@media (min-width: 1080px) {
  .menu__item > li:hover .menu__submenu, .menu__item > li:focus .menu__submenu {
    opacity: 1;
    pointer-events: auto;
  }
}
.menu__item > li + li {
  margin-left: 1.9375rem;
}
@media only screen and (max-width: 1079px) {
  .menu__item > li + li {
    margin-left: 0;
    border-top: 1px solid #e9e9e9;
  }
}
.menu__item > li > a,
.menu__item > li > span {
  font-size: 1rem;
  line-height: 1rem;
  font-weight: 500;
}
.menu__item > li > a {
  transition: color 0.3s ease;
}
@media (min-width: 1080px) {
  .menu__item > li > a:hover, .menu__item > li > a:focus {
    color: #969696;
  }
}
.menu__item > li > span {
  display: flex;
  align-items: center;
}
.menu__item > li > span svg {
  margin-left: 0.3125rem;
}
.menu__submenu {
  min-width: 11rem;
  opacity: 0;
  pointer-events: none;
  border: 1px solid #e9e9e9;
  background-color: #ffffff;
  padding: 1.3125rem 1.25rem;
  position: absolute;
  top: 100%;
  transition: opacity 0.3s ease;
}
@media only screen and (max-width: 1079px) {
  .menu__submenu {
    display: none;
    min-width: 100%;
    width: 100%;
    opacity: 1;
    position: relative;
    top: 0;
    border: 0;
    padding: 1.3125rem 0;
    pointer-events: auto;
  }
}
.menu__submenu li + li a {
  border-top: 1px solid #e9e9e9;
  margin-top: 0.5rem;
  padding-top: 0.5rem;
}
.menu__submenu a {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 0.8125rem;
  line-height: 0.8125rem;
  transition: color 0.3s ease, padding-left 0.3s ease;
}
@media (min-width: 1080px) {
  .menu__submenu a:hover, .menu__submenu a:focus {
    color: #969696;
    padding-left: 0.375rem;
  }
}
.menu__submenu a svg {
  margin-left: 0.625rem;
}

/* Blocks */
.posts {
  margin-top: 3rem;
}
.posts__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.posts__item {
  width: 31%;
  margin-bottom: 2.875rem;
}
@media only screen and (max-width: 1079px) {
  .posts__item {
    width: 48%;
  }
}
@media only screen and (max-width: 767px) {
  .posts__item {
    width: 100%;
  }
}

@media (min-width: 1080px) {
  .post:hover .post__image, .post:focus .post__image {
    box-shadow: 0 0 13px 0 rgba(34, 60, 80, 0.2);
  }
}
.post__image {
  height: 0;
  padding-top: 64%;
  position: relative;
  transition: box-shadow 0.3s ease;
}
@media only screen and (max-width: 1079px) {
  .post__image {
    padding-top: 51%;
  }
}
@media only screen and (max-width: 767px) {
  .post__image {
    padding-top: 60%;
  }
}
.post__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.post__tag {
  margin-top: 1.0625rem;
}
.post__tag a {
  font-size: 0.8125rem;
  line-height: 0.8125rem;
  font-weight: 700;
  color: #eb0028;
  transition: opacity 0.3s ease;
}
@media (min-width: 1080px) {
  .post__tag a:hover, .post__tag a:focus {
    opacity: 0.8;
  }
}
.post__title {
  display: inline-block;
  font-size: 1.5rem;
  line-height: 1.875rem;
  font-weight: 700;
  margin-top: 1rem;
  transition: color 0.3s ease;
}
@media (min-width: 1080px) {
  .post__title:hover, .post__title:focus {
    color: #969696;
  }
}
.post__info {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  font-size: 0.75rem;
  line-height: 0.75rem;
  margin-top: 1rem;
}
.post__author {
  font-weight: 500;
}
.post__date, .post__views {
  display: inline-flex;
  align-items: center;
  color: #9b9b9b;
}
.post__date::before, .post__views::before {
  content: "";
  display: block;
  width: 0.1875rem;
  height: 0.1875rem;
  background-color: #d7d7d7;
  margin: 0 0.3125rem;
}
.post__text {
  font-size: 0.875rem;
  line-height: 1.25rem;
  color: #929292;
  margin-top: 1.0625rem;
}